export default function detailMap() {
  let target = document.querySelector('#js-map-iioffice');
  if (!target) return;
  
  let targetAddress = target.getAttribute('data-address');
  google.maps.event.addDomListener(window, 'load', initMap);

  function initMap() {
    let map = new google.maps.Map(document.getElementById('js-map-iioffice'), {
      zoom: 6,
      center: {lat: 35.710280295646456, lng: 139.77718591690063}
    });
    let geocoder = new google.maps.Geocoder();

    geocodeAddress(geocoder, map);
  }

  function geocodeAddress(geocoder, resultsMap) {
    geocoder.geocode({'address': targetAddress}, function(results, status) {
      if (status === 'OK') {
        resultsMap.setZoom(17);
        resultsMap.setCenter(results[0].geometry.location);
        let marker = new google.maps.Marker({
          map: resultsMap,
          position: results[0].geometry.location
        });
      } else {
        alert('Address not found');
      }
    });
  }
}


